<template>

<!--下载饿了么页面--->
 <div class="download">

   <!--导航栏-->
   <van-nav-bar
     title="下载"
     left-text=" "
     arrow-left
     left-arrow
     @click-left="onClickLeft"
   />

   <!--饿了么图标-->
       <div class="content">
         <img src="data:image/jpeg;base64," alt="">
         <p>下载饿了么APP</p>

         <div @click="Down">下载</div>


       </div>
   <div v-if="show1">
     <!--单个弹框信息-->
     <transition mode="out-in" enter-active-class="animated heartBeat">
       <div  class="MyAlter" >
         <svg t="1564970482842" class="icon AT" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2031" width="70" height="70"><path d="M508.8 44.8c-256 0-464 208-464 464s208 464 464 464 464-208 464-464-208-464-464-464z m0 880c-230.4 0-416-185.6-416-416s185.6-416 416-416 416 185.6 416 416-185.6 416-416 416z" fill="#F8CB86" p-id="2032"></path><path d="M521.6 652.8c12.8 0 22.4-9.6 22.4-22.4V201.6c0-12.8-9.6-22.4-22.4-22.4-12.8 0-22.4 9.6-22.4 22.4v428.8c-3.2 9.6 9.6 22.4 22.4 22.4zM486.4 748.8a1.1 1.1 0 1 0 70.4 0 1.1 1.1 0 1 0-70.4 0z" fill="#F8CB86" p-id="2033"></path></svg>
         <p>{{popKuang}}</p>
         <div @click="yes">确定</div>
       </div>
     </transition>

   </div>


 </div>




</template>

<script>
  import {Dialog} from 'vant';



    export default {
        name: "downloadELM",
      data() {
        return {
          show: false,
          show1:false,
          popKuang:"IOS用户请前往AppStore下载"
        }
      },
      methods: {
        Down(){
          this.show1=true
        },
        yes(){
          this.show1=false
        },
        onClickLeft() {
          this.$router.go(-1);
        },
        showPopup() {
          this.show = true;
        },
      },
      comments:{
        [Dialog.Component.name]: Dialog.Component
      }
    }
</script>


<style scoped lang="less">
  @import "../FZ/popKuang";
  .van-nav-bar{
    background: #3190e8;
    color: white;

    :before{
      color: white;
      height: 0.2344rem;
      font-size: 0.25rem;
      font-weight:200;
      margin-left: -0.1rem;
      margin-top: -0.1rem;
    }
    span,div{
      color: white;
    }
    div{
      font-weight: 800;
      font-size: 0.1875rem;
    }
  }
/*剩余标内容区*/
  .content{
    height: 2.9968rem;
    text-align: center;

    img{
      height: 1.8rem;
      margin-top: 0.24375rem;
      border-radius: 0.05rem;
    }
    p{
      font-family: Helvetica Neue,Tahoma,Arial;
      font-size: 0.1875rem;
      margin-bottom: 0.234375rem;
      margin-top: 0.1rem;
      color: #666;
    }
    div{
      width: 3.424rem;
      height: 0.416rem;
      color: #FFF;
      margin-top: 0.1171rem;
      background: #4CD964;
      margin: auto;
      line-height: 0.416rem;
      border-radius: 0.05rem;
    }
  }

</style>



