<template>
  <!--这是我的优惠组件-->
  <div class="LmyDiscounts">
    <!--导航栏-->
    <van-nav-bar
      title="我的优惠"
      left-text=" "
      arrow-left
      left-arrow
      @click-left="onClickLeft"
    />
    <!--红包、商家代金券-->
    <van-tabs @click="onClick" title-active-color="#3190e8" title-inactive-color="black" line-width="0.7rem"
              color="#3190e8">
      <!--红包内容-->
      <van-tab title="红包">
        <div class="content">
          <!--红包到期和说明-->
          <div class="explain">
            <span>有 <b>{{redP.length}}</b>个红包即将到期</span>

            <router-link :to="{path:'HBexplain'}">
              <span class="HBexplain">红包说明</span>
            </router-link>

            <img :src="request" alt="">
          </div>


          <!--红包-->
          <div class="redPacket" v-for="(value,index) in redP ">
            <!--左侧-->
            <div class="red_left">
              <span>￥<b>{{value.amount}}</b></span>
              <p>满{{value.sum_condition}}元可用</p>
            </div>
            <!--中间-->
            <div class="red_center">
              <span>{{value.name}}</span>
              <p>{{value.end_date}}</p>
              <p>限收货手机号为{{value.phone}}</p>
            </div>
            <!--右边-->
            <div class="red_right">
              {{value.description_map.validity_delta}}
            </div>
          </div>


          <!--红包限制-->
          <p class="remark">限品类：快餐便当、特色菜系、小吃夜宵、甜品饮品、异国料理</p>

        </div>

        <div class="history">
          <router-link :to="{path: 'noTimeHb'}">查看历史红包></router-link>
        </div>
        <!--兑换红包 推荐有奖-->
        <div class="bottom">
          <router-link :to="{path:'changePackage'}">兑换红包</router-link>
          <router-link :to="{path:'commend'}">推荐有奖</router-link>

        </div>

      </van-tab>


      <!--商家代金券内容-->
      <van-tab title="商家代金券">
        <div class="voucher">
          <!--红包说明-->
          <div class="explain">
            <span class="redP">红包说明</span>
            <img :src="request" alt="">
          </div>

          <img
            src="data:image/png;base64,"
            alt="">
          <p class="voucher_p1">无法使用代金券</p>
          <p class="voucher_p2">非客户端或客户端版本过低</p>
          <router-link :to="{path:'downloadELM'}" class="voucher_a">下载或升级客户端</router-link>

        </div>


      </van-tab>
    </van-tabs>

  </div>
</template>

<script>
  import {mapMutations, mapState} from "vuex";
  import {getHongbaoNum} from "../../service/getData";

  const request = require("@/assets/img/request.png");
  const packet = require("@/assets/img/bg-packet.png");

  export default {
    name: "LmyDiscounts",
    data() {
      return {
        request,
        packet,
        allInfo: {},
        redP: [],
      }
    },
    mounted() {
      this.initData();
    },
    methods: {
      ...mapMutations(['changeHbNumber']),
      async initData() {
        let user_id = this.USER_STATUS.id;

        //获取红包假数据
        let hb = await getHongbaoNum(user_id);
        console.log(hb);
        if (hb.length > 0) {
          this.redP = hb;
          this.changeHbNumber(hb);
        }
      },
      onClickLeft() {
        this.$router.go(-1);
      },
      onClick(name, title) {

      }
    },
    computed: {
      ...mapState(['USER_STATUS']),
    }
  }
</script>

<style scoped lang="less">、
.LmyDiscounts {
  width: 100%;
}

.van-nav-bar {
  background: #3190e8;
  color: white;

  :before {
    color: white;
    height: 0.2344rem;
    font-size: 0.25rem;
    font-weight: 200;
    margin-left: -0.1rem;
    margin-top: -0.1rem;
  }

  span, div {
    color: white;
  }

  div {
    font-weight: 800;
    font-size: 0.1875rem;
  }
}

/*红包中间部分*/
.content {
  width: 100%;
  height: 3.9434rem;
  box-sizing: border-box;
  padding: 0 0.164rem;

  /*红包*/

  .redPacket {
    height: 0.9739rem;
    padding: 0.23rem 0.1172rem 0.1875rem;
    box-sizing: border-box;
    border-radius: 0.06rem;
    background: url("../../assets/img/bg-packet.png") repeat-x top center;
    background-size: 0.12rem;
    margin-bottom: 0.117rem;

    > div:nth-child(1) {
      display: inline-block;
      width: 0.8rem;
      height: 0.552rem;
      color: red;
      border-right: 1px dashed #999;
      /*￥*/

      span {
        font-size: 0.1875rem;

        b {
          font-size: 0.3515rem;
        }
      }

      p {
        font-size: 0.096rem;
        color: #999;
        margin-top: 0.1rem;

      }
    }

    > div:nth-child(2) {
      width: 1.58rem;
      height: 0.552rem;
      display: inline-block;
      margin-left: 0.1rem;

      span {
        margin-top: -15px;
        color: #666;
      }

      p {
        font-size: 0.096rem;
        color: #999;
      }

      > p:nth-child(2) {
        line-height: 0.2rem;

      }
    }

    > div:nth-child(3) {
      display: inline-block;
      /*height: 1rem;*/
      /*margin-bottom: 0.5rem;*/
      /*line-height: 0.552rem;*/
      vertical-align: 0.15rem;
      color: red;
      font-size: 0.175rem;

    }
  }
}

/*红包限制*/
.remark {
  font-size: 0.096rem;
  color: #999;
  /*margin-left: 0.1rem;*/
  margin: 0.09375rem;
}

/*查看历史红包*/
.history {
  width: 3.75rem;
  height: 0.92rem;

  a {
    width: 100%;
    display: inline-block;
    text-align: center;
    margin: auto;
    line-height: 0.92rem;
    color: #555;
    font-size: 0.1171rem;

  }
}

/*兑换红包 推荐有奖*/
.bottom {
  height: 0.464rem;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: bisque;
  font-size: 0.164rem;

  a {
    width: 49.2%;
    color: #555;
    display: inline-block;
    text-align: center;
    line-height: 0.464rem;
    background: #ffffff;

  }
}

/*两个共同的红包说明*/
.explain {
  width: 3.424rem;
  height: 0.472rem;

  > span:nth-child(1) {
    color: #666;
    font-size: 0.1175rem;
    line-height: 0.472rem;

    b {
      color: red;
    }
  }

  .HBexplain {
    float: right;
    font-size: 0.1175rem;
    line-height: 0.472rem;
    color: #3190e8;
  }

  img {
    float: right;
    margin-top: 0.17rem;
    margin-right: 0.04rem;
  }
}

.redP {
  float: right;
}

/*商家代金券*/
.voucher {
  height: 3.1766rem;
  text-align: center;

  > img:nth-child(2) {
    display: inline-block;
    margin-top: 0.9375rem;
    height: 0.7969rem;
  }

  .voucher_p1 {
    margin-top: 0.0937rem;
    color: #666;
    font-size: 0.164rem;
  }

  .voucher_p2 {
    margin: 0.07rem;
    margin-bottom: 0.1rem;
    color: #999;
    font-size: 0.117rem;
    line-height: 0.12rem;
  }

  .voucher_a {
    width: 1.35rem;
    height: 0.345rem;
    background: #56D176;
    display: inline-block;
    color: white;
    line-height: 0.345rem;
    font-size: 0.1523rem;
    border-radius: 4px;
  }
}

</style>



