<template>
  <!--我的余额组件-->
  <div class="MyBalance">

    <!--导航-->
    <van-nav-bar
      title="我的积分"
      left-text=" "
      arrow-left
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="header">
      <!--头部白色区域-->
      <div class="header_content">
        <p>
          <span>当前积分</span>
          <router-link :to="{path:'balanceExplain'}" class="explain">积分说明</router-link>
