        </p>
        <!--白色区域中间金额部分-->
        <div class="num">
          <span>0</span>
          <span>分</span>
        </div>
        <button>积分兑换商品</button>
      </div>
    </div>
    <!--交易明细-->
    <p class="detail">最近30天积分记录</p>
    <!--图片和暂无明细记录-->
    <div class="footer">
      <img src="data:image/png;base64," alt="">
      <p>暂无明细记录</p>
    </div>

  </div>


</template>

<script>
  import Lnavigation from "./Lnavigation";
  const packet = require("@/assets/img/bg-packet.png");
  export default {
    name: "myNumber",
    components: {Lnavigation},
    data(){
      return{
        abc:"123",
        packet
      }
    },
    methods: {
      onClickLeft() {
        this.$router.go(-1);
      },
    }
  }
</script>

<style scoped lang="less">
  /*导航栏*/
  .van-nav-bar{
    background: #3190e8;
    color: white;

    :before{
      color: white;
      height: 0.2344rem;
      font-size: 0.25rem;
      font-weight:200;
      margin-left: -0.1rem;
      margin-top: -0.1rem;
    }
    span,div{
      color: white;
    }
    div{
      font-weight: 800;
      font-size: 0.1875rem;
    }
  }

  .header{
    /*background: url("../../assets/img/bg-packet.png") top center repeat-X;*/
    /*border-radius: 0.05rem;*/
    width: 100%;
    height: 1.58rem;
    background: #3190e8;
    padding: 0.07rem;
    .header_content{
      padding-top: 0.1rem;
      box-sizing: border-box;
      width: 3.6rem;
      height: 1.57rem;
      border-radius: 0.05rem;
      padding: 0.09375rem;
      background: white;
      /*当前余额 余额说明*/
      p{
        width: 3.424rem;
        height: 0.168rem;
        span{
          float: left;
          font-size: 0.128rem;
          color: #666;
        }

        .explain{
          float: right;
          font-size: 0.128rem;
          color: #3190e8;
        }
        .icon{
          /*width: 0.14rem;*/
          float: right;
        }
      }
      /*金额 /元*/
      .num{
        width: 3.424rem;
        height: 0.512rem;
        line-height: 0.512rem;

        >span:nth-child(1){
          font-size: 0.421rem;
        }
      }
      /*提交按钮*/
      button{
        width: 3.424rem;
        height: 0.4687rem;
        margin-top: 0.23438rem;
        border-radius: 3px;
        border: none;
        background: #FE6D47;
        color: white;
        font-size: 0.187rem;
      }
    }

  }

  /*最近30天积分记录*/
  .detail{
    width: 100%;
    height: 0.464rem;
    color: #999999;
    font-size: 0.1406rem;
    line-height: 0.464rem;
    padding-left: 0.117rem;
  }


  .footer{
    width: 100%;
    height: 1.57rem;
    /*background: wheat;*/
    margin-top: 0.2438rem;
    img{
      width: 1.875rem;
      height: 1.179rem;
      text-align: center;
      margin-left: 0.8rem;
    }
    p{
      margin-top: 0.11719rem;
      font-size: 0.164rem;
      color: #666;
      text-align: center;
      margin-left: -0.3rem;
    }
  }
</style>



